body {
  margin: 0;
  font-family: 'Poppins', sans-serif;
  background: linear-gradient(135deg, #1e1f29, #252738, #3a3c54);
  color: white;
  min-height: 100vh;
}

.app-container {
  padding: 40px 20px;
  max-width: 1200px;
  margin: auto;
}

.title {
  text-align: center;
  font-size: 36px;
  background: linear-gradient(to right, #7f5af0, #2cb67d);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  margin-bottom: 40px;
}

.calendar-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 25px;
}

.day-card {
  background: rgba(255, 255, 255, 0.05);
  border-radius: 16px;
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  padding: 20px;
  transition: 0.3s;
}

.day-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.3);
}

.day-name {
  font-size: 22px;
  color: #a6accd;
  margin-bottom: 15px;
  text-transform: capitalize;
}

.input-group {
  display: flex;
  gap: 8px;
  margin-bottom: 10px;
}

.input-group input {
  flex: 1;
  padding: 10px;
  border-radius: 10px;
  border: none;
  font-size: 15px;
  background: rgba(255, 255, 255, 0.1);
  color: white;
}

.input-group button {
  background: #7f5af0;
  border: none;
  color: white;
  border-radius: 10px;
  padding: 0 12px;
  font-size: 18px;
  cursor: pointer;
  transition: 0.2s;
}

.input-group button:hover {
  background: #624ae0;
}

.task-list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.task-item {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 6px 0;
  border-bottom: 1px solid rgba(255, 255, 255, 0.05);
}

.task-item input[type="checkbox"] {
  accent-color: #2cb67d;
  transform: scale(1.2);
}

.task-item span {
  flex: 1;
}

.task-item span.done {
  text-decoration: line-through;
  color: #888;
}

.delete-btn {
  background: transparent;
  border: none;
  font-size: 16px;
  cursor: pointer;
  color: #ff6b6b;
}
